import { getComments } from '../../api/article';
import { mapState } from 'vuex';

export default {
  computed: {
    ...mapState(['user'])
  },
  props: {
    article: {
      type: Object,
      required: true
    }
  },

  data() {
    return {
      comments: []
    }
  },

  async mounted() {
    const { data } = await getComments(this.article.slug);
    console.log(data, 'data')

    this.comments = data.comments
  }
}
