export default {
  name: 'userMetaComponent',
  head() {
    return {
      title: `${this.article.title} - RealWorld`,
      meta: [
        { hid: 'description', name: 'description', content: this.article.description}
      ]
    }
  },
  props: {
    article: {
      type: Object,
      required: true
    }
  }
}
